	            var index = square - (square - 17) + 128;
	            g_vectorDelta[index].pieceMask[colorWhite >> 3] |= 1 << piecePawn;
	            index = square - (square - 15) + 128;
	            g_vectorDelta[index].pieceMask[colorWhite >> 3] |= 1 << piecePawn;
	
