	            var rook = g_board[to - 2];
	
	            g_hashKeyLow ^= g_zobristLow[to - 2][rook & 0xF];
	            g_hashKeyHigh ^= g_zobristHigh[to - 2][rook & 0xF];
	            g_hashKeyLow ^= g_zobristLow[to + 1][rook & 0xF];
	            g_hashKeyHigh ^= g_zobristHigh[to + 1][rook & 0xF];
	
