	            g_board[to - 2] = pieceEmpty;
	
	            g_baseEval -= pieceSquareAdj[rook & 0x7][me == 0 ? flipTable[to - 2] : to - 2];
	            g_baseEval += pieceSquareAdj[rook & 0x7][me == 0 ? flipTable[to + 1] : to + 1];
	
