	    g_hashKeyHigh ^= g_zobristHigh[from][piece & 0xF];
	    g_hashKeyLow ^= g_zobristLow[to][piece & 0xF];
	    g_hashKeyHigh ^= g_zobristHigh[to][piece & 0xF];
