	        if (flags & moveflagPromoteKnight) newPiece |= pieceKnight;else if (flags & moveflagPromoteQueen) newPiece |= pieceQueen;else if (flags & moveflagPromoteBishop) newPiece |= pieceBishop;else newPiece |= pieceRook;
	
	        g_hashKeyLow ^= g_zobristLow[to][piece & 0xF];
	        g_hashKeyHigh ^= g_zobristHigh[to][piece & 0xF];
